<template>
  <div class="detail">
    <Header/>
    <div class="container p-3">
      <div class="d-flex">
        <img v-if="detail.gender === 'F'" src="https://w0.pngwave.com/png/885/863/computer-icons-icon-design-user-female-woman-png-clip-art.png" height="20px" alt="">
        <img v-else src="https://w0.pngwave.com/png/163/442/computer-icons-user-profile-icon-design-avatar-png-clip-art-thumbnail.png" height="20px" alt="">
        <p class="h6 text-gray-500 ml-2 mb-0">{{ detail.anonymousSchool === true ? '匿名': detail.school }}</p>
      </div>
      <p class="h5 text-left my-3">{{detail.title}}</p>
      <p class="h6 text-left text-gray-dark" v-html="detail.excerpt"></p>
    </div>
  </div>
</template>

<script>
// @ is an alias to /src
import Header from '@/components/Header.vue'
import axios from 'axios'

export default {
  name: 'Detail',
  components: {
    Header
  },
  data () {
    return {
      postID: 0,
      detail: {}
    }
  },
  created () {
    console.log(this.$route.query.postID)
    this.postID = this.$route.query.postID
    this.getdata()
  },
  methods: {
    getdata () {
      console.log('this.postID')
      axios.get(`https://www.dcard.tw/service/api/v2/posts/${this.postID}`).then(res => {
        console.log('res', res)
        this.detail = res.data
      })
    }
  }
}
</script>
